.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  align-items: start;

  @include onTablet {
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }

  @include onDesktop {
    grid-template-columns: repeat(12, 1fr);
  }

  &--desktop {
    display: block;
    
    
    @include onTablet {
      display: grid;
    }
  }

  &__item {
    &--first-bike {
      @include onTablet {
        grid-column: 2 / 6;
      }
    }

    display: block;
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        
        &--tablet-#{$start}-#{$end} {
          @include onTablet {
            grid-column: #{$start} / #{$end + 1};
          }
        }

        &--desktop-#{$start}-#{$end} {
          @include onDesktop {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }
}
